import React from 'react';
import KeyboardEventHandler from 'react-keyboard-event-handler';
import {DataService} from '../services/DataService';

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.resetCounter = this.resetCounter.bind(this)
        this.getGymPhysicalFitnessCenter = this.getGymPhysicalFitnessCenter.bind(this)

        this.state = { handleKeys : ['enter','z','c'], coaches:[] }
        this.props.parent.currentPage = this;
        this.props.parent.setState({"availableKeys" : this.state.handleKeys, coachid:null, cid:null, ccid:null,
        user:{userId:null,age:null,gender:null}
    })

        this.buttons={}
    }

    async getGymPhysicalFitnessCenter(qrcodeContent){
        if (qrcodeContent.startsWith("https://qr.accuniq.com/accuniq?bcadata=")) {
            let bcadata = qrcodeContent.substring("https://qr.accuniq.com/accuniq?bcadata=".length)
            var indbody = [
                // 0-9
                {l:2,f:''},{l:6,f:''},{l:10,f:''},{l:20,f:''},{l:1,f:''},
                {l:3,f:''},{l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},
                // 10-19
                {l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},
                {l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},

                // 20-29
                {l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},
                {l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},
                // 30-39
                {l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},
                {l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},
                // 40-49
                {l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},
                {l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},{l:4,f:3},
                // 50-59
                {l:4,f:3},{l:4,f:3},{l:20,f:''},{l:3,f:1},{l:12,f:''},
                {l:16,f:''},{l:2,f:''},{l:3,f:''},{l:13,f:''},{l:3,f:2},
                // 60-69
                {l:16,f:''},{l:3,f:0},{l:17,f:''},{l:3,f:''},{l:4,f:''},
                {l:4,f:''},{l:1,f:''},{l:4,f:3},{l:5,f:4},{l:5,f:4},
                // 70-79
                {l:5,f:4},{l:5,f:''},{l:5,f:''},{l:4,f:''},{l:5,f:4},
                {l:5,f:4},{l:4,f:3},{l:4,f:3},{l:4,f:''},{l:3,f:''},
                // 80-89
                {l:3,f:''},{l:3,f:''},{l:3,f:''},{l:3,f:''},{l:3,f:''},
                {l:3,f:''},{l:3,f:''},{l:3,f:''},{l:3,f:''},{l:3,f:''},
                // 90-99
                {l:3,f:''},{l:3,f:''},{l:3,f:''},{l:3,f:''},{l:4,f:''},
                {l:3,f:''},{l:73,f:''}

                ]
                var result = [];

                var index =0;
                for(var i=0;i<indbody.length;i++){
                    if(indbody[i].l){
                        result[i] = bcadata.substring(index,index+indbody[i].l)
                        index += indbody[i].l;
                        if(indbody[i].f !== ''){
                            if (indbody[i].f === 0){
                                result[i] = "0."+result[i]
                            }else{
                                result[i] = result[i].substr(0,indbody[i].f)+"."+result[i].substr(indbody[i].f)
                            }
                        }
                    } else {
                        result[i] = null
                    }
                }

                this.props.parent.setState({user:{userId:result[3].replace(/\*/g,""),age:result[5],gender:result[4]}})
                console.log( result )
        } else if(Number.isNaN( parseInt(qrcodeContent) ) === false ) {
            this.props.parent.setState({user:{userId:qrcodeContent,age:null,gender:null}})
        } else if( Number.isNaN( parseInt(qrcodeContent) ) && (qrcodeContent.startsWith("P") || qrcodeContent.startsWith("T") ) ){
            this.props.parent.setState({user:{userId:qrcodeContent,age:null,gender:null}})
        }
    }

    resetCounter(){
    }

    async componentDidMount() {
        await DataService.loadJSON()
        this.setState({coaches: DataService.getCoachList()})
        window.setElementEventLintener("Home")

        var keys = Object.keys(this.buttons);
        if (keys.length > 0){
            if (this.state.coachid){
                for(let i=0;i<keys.length;i++){
                    if( keys[i] === this.state.coachid){
                        this.buttons[keys[i]].focus();
                        break;
                    }
                }
            } else
                this.buttons[keys[0]].focus();
        }
    }

    componentWillUnmount(){
    }

    render(){
        return (<KeyboardEventHandler
                handleKeys={this.state.handleKeys}
                onKeyEvent={(key, e, page) => this.props.util(key, e, this, page)} >
                <div style={{backgroundImage:'url(./assets/v_bg.jpg)', width:1080, height:1920}}>
                    <div className="home_selectCoach">
                        請選擇教練
                    </div>

                    <div className="home_selectCoachAreaSize" >
                        {
                            this.state.coaches.map((item,index) => {
                                var attr = '{"coachid":"'+item.id+'"}'
                                let avatar_url = "./course"+item.avatar
                                return <div className="home_coachArea"
                                            tabIndex={index}
                                            key={index}
                                            attr={attr}
                                            row_index={0}
                                            col_index={index}
                                            col_max={2}
                                            ref={(input) => { this.buttons[item.id] = input }} href="#"
                                        >

                                            <div className="home_videoArea" >
                                                <img src={avatar_url} alt="coach" style={{height:"100%",opacity:0.3}} ></img>
                                            </div>

                                    </div>
                            })
                        }
                    </div>
                    {
                        this.props.parent.state.user.userId ?
                        <div className="home_auth">
                            歡迎 {this.props.parent.state.user.userId} 登入系統
                        </div>
                        :
                        <div className="home_auth">
                            請對著鏡頭出示 BC380 報告之QRCode <br/>作登入之身份認證
                        </div>
                    }


                    <video className="home_tour" loop={true} controls={false} muted={true} autoPlay={true}>
                        <source src="/assets/homedir.mp4"></source>
                    </video>
                </div>
            </KeyboardEventHandler>)
    }
}

export default Home;
