import React from 'react';
import './App.css';

import tools from './pages/util.js'

import Home from './pages/Home.js'
import ClassBegin from './pages/ClassBegin';
import Class2 from './pages/Class2';
import ClassDone from './pages/ClassDone';

class App extends React.Component {

  constructor(props) {
    super(props);

    this.state = {page: 1,
                  availableKeys:[],"lastActionTime":"",
                  isDeveMode: process.env.NODE_ENV !== 'production',
                  doExercise:false, websocketip:'127.0.0.1:5500',
                  pageIndex:{
                    "Home":1,
                    "Class2":2,
                    "ClassBegin":3,
                    "ClassDone":4
                  },
                  user:{userId:null,age:null,gender:null}
                } ;

    this.useWebSocket = this.useWebSocket.bind(this);
    this.changePage = this.changePage.bind(this);
    this.currentPage = null;
  }

  componentDidMount() {
    if ( this.state.isDeveMode === false){
      this.useWebSocket(this)
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if ( this.state.isDeveMode === false){
      this.useWebSocket(this)
    }
  }
  componentWillUnmount() {

  }

  keyEvent = ((key, e, page) => tools.Util.KeyboardEventHandler_onKeyEvent(key, e,this,page))

  useWebSocket(app){
    let _app = this

    if (this.ws == null){
      this.ws = new WebSocket("ws://"+_app.state.websocketip+"/api");

      this.ws.onopen = function() {
        console.log("onopen")
      };

      this.ws.onmessage = function(e) {

        let json = JSON.parse(e.data)
        if (json['function'] === 'exercise_status' && app.state.doExercise === false) {
          var key = json['最後動作']
          var currentFoucs = window.$(':focus')[0]
          console.log( key, currentFoucs)
          switch(key){

            /*case "左手彎舉":
              console.log('case 左手彎舉 and 向上選取')
              if (_app.state.availableKeys.includes('s')){ //up
                console.log('case 左手彎舉 in if')
                _app.keyEvent('s', {"target":currentFoucs},_app.currentPage)
              }
              break;*/

            case "向上選取":
              console.log('case 向上選取')
              if (_app.state.availableKeys.includes('s')){ //up
                console.log('case 向上選取 in if')
                _app.keyEvent('s', {"target":currentFoucs},_app.currentPage)
              }
              break;

            /*case "右手彎舉":
              console.log('case 右手彎舉 and 向下選取')
              if (_app.state.availableKeys.includes('x')){ //down
                console.log('case 向下選取 in if')
                _app.keyEvent('x', {"target":currentFoucs},_app.currentPage)
              }
              break;*/

            case "向下選取":
              console.log('case 向下選取')
              if (_app.state.availableKeys.includes('x')){ //down
                console.log('case 向下選取 in if')
                _app.keyEvent('x', {"target":currentFoucs},_app.currentPage)
              }
              break;

            /*case "雙手交叉":
              console.log('case 雙手交叉 and 取消')
              if (_app.state.availableKeys.includes('esc')){
                console.log('case 取消 in if')
                _app.keyEvent('esc', {"target":currentFoucs},_app.currentPage)
              }
              break;*/

            case "取消":
              console.log('case 取消')
              if (_app.state.availableKeys.includes('esc')){
                console.log('case 取消 in if')
                _app.keyEvent('esc', {"target":currentFoucs},_app.currentPage)
              }
              break;

            case "向右選取":
              console.log('case 向右選取')
              if (_app.state.availableKeys.includes('z')){ // 'left'
                console.log('case 向右選取 in if')
                _app.keyEvent('z', {"target":currentFoucs},_app.currentPage)
              }
              break;
            case "向左選取":
              console.log('case 向左選取')
              if (_app.state.availableKeys.includes('c')){ // right
                console.log('case 向左選取 in if')
                _app.keyEvent('c', {"target":currentFoucs},_app.currentPage)
              }
              break;


            /*case "雙手彎舉":
              console.log('case 雙手彎舉 and 點選')
              console.log('case 點選')
              if (_app.state.availableKeys.includes('enter')){
                console.log('case 點選 in if')
                _app.keyEvent('enter', {"target":currentFoucs},_app.currentPage)
              }
              break;*/

            case "點選":
              console.log('case 點選')
              if (_app.state.availableKeys.includes('enter')){
                console.log('case 點選 in if')
                _app.keyEvent('enter', {"target":currentFoucs},_app.currentPage)
              }
              break;

            default:
              break;
          }
        } else if (json['function'] === 'getExercise') {
          console.log( 'function === getExercise', json['function'] === 'getExercise' )
          if (typeof _app.currentPage.getExerciseWebSockit === "function" ){
            _app.currentPage.getExerciseWebSockit(json)
          }
        } else if (json['function'] === 'qrcode') {
          console.log( 'function === qrcode', json['function'] === 'qrcode' )

          if (typeof _app.currentPage.getGymPhysicalFitnessCenter === "function" ){
            _app.currentPage.getGymPhysicalFitnessCenter(json['content'])
          }

        } else {
          console.log( 'function Error ' )
        }
      };

      this.ws.onclose = function() {
        _app.ws = null
        _app.useWebSocket(_app)
      };

      this.ws.onerror = function(e) {
      };
    }
  }

  changePage(page){
    this.setState( {page: page})
  }

  render() {
    const currentPageIndex = this.state.page;

    return (
      <div className="App" style={{maxWidth:1080, maxHeight:1920, minHeight:1920,minWidth:1080}} >
        { currentPageIndex === this.state.pageIndex.Home
            ? <Home util={this.keyEvent} parent={this} ></Home>
            : currentPageIndex === this.state.pageIndex.Class2
            ? <Class2 util={this.keyEvent} parent={this} ></Class2>
            : currentPageIndex === this.state.pageIndex.ClassBegin
            ? <ClassBegin util={this.keyEvent} parent={this} ></ClassBegin>
            : <ClassDone util={this.keyEvent} parent={this} ></ClassDone> }
      </div>
    );
  }
}

export default App;
