import React from 'react';
import KeyboardEventHandler from 'react-keyboard-event-handler';
import tools from './util';

import {DataService} from '../services/DataService';

class Catalog extends React.Component {
    constructor(props) {
        super(props);
        this.resetCounter = this.resetCounter.bind(this)

        this.app = this.props.parent;
        this.state = { "countDown": 30, handleKeys : [/*'up','down'*/'s','x','enter','z','c'/*'left','right'*/,'esc'], "courseList":[] }
        this.buttons = {}
        this.props.parent.currentPage = this;
        this.props.parent.setState({
            "availableKeys" : this.state.handleKeys,
            "log":{
                'start_time':tools.Util.DateTimeFormat(new Date(),"yyyy/MM/dd hh:mm:ss"),
                'end_time':'',
                'elapsed_time':'',
                'course':'',
                'page':''
            }
        })
    }

    resetCounter(){
        this.setState({"countDown":30})
    }

    async componentDidMount() {
        await DataService.loadJSON()

        let courseList = await DataService.getCourseList2(this.props.parent.state.coachid);
        this.setState({"courseList": courseList})

        if (this.props.parent.state.isDeveMode === false){
            var interval = window.setInterval(async ()=>{
                if ( this.state != null ){
                    var newCountDown = this.state.countDown -1
                    this.setState({ "countDown": newCountDown})
                    if ( newCountDown === 0){
                        let log = this.props.parent.state.log;
                        log['end_time'] = tools.Util.DateTimeFormat(new Date(),"yyyy/MM/dd hh:mm:ss")
                        log['elapsed_time'] = ( Date.parse(log.end_time) - Date.parse(log.start_time) ) / 1000
                        log['page'] = 'Page 2';
                        this.app.changePage( this.app.state.pageIndex.Home )
                        tools.Util.sendLogNoScore(log);
                    }
                }
            }, 1000)

            this.setState({"interval":interval})
        }

        setTimeout( ()=>{
            window.setElementEventLintener("Class2")

            if (this.props.parent.state.cid && this.props.parent.state.ciid) {
                tools.Util.Class2Focus(this.buttons[this.props.parent.state.cid+"_"+this.props.parent.state.ciid])
            } else
                this.buttons[Object.keys(this.buttons)[0]].focus();

        }, 500)
    }

    componentWillUnmount(){
        if (this.props.parent.state.isDeveMode === false){
            if (this.state.interval)
                window.clearInterval( this.state.interval )
        }
    }

    async mycallback(title){
        if (this.props.parent.state.isDeveMode === false){
            this.app.ws.send( JSON.stringify({"function":"uploadCourse", "name":encodeURI(title)} ))
        }
    }

    render(){
        let courseSeqIndex = 0;
        return <KeyboardEventHandler
                    handleKeys={this.state.handleKeys}
                    onKeyEvent={(key, e, page) => this.props.util(key, e, this, page)} >
                    <div style={{backgroundImage:'url(./assets/v_bg.jpg)', width:1080, height:1920}}>

                        <div className="class2_area" >
                        {
                            this.state.courseList.map((item,index) => {
                                var attr = '{"cid":"'+item.id+'"}'
                                return <div className="class2_catalog_area" attr={attr} key={item.id}>
                                            <div className="class2_catalog" row_index = {index} row_header="true" >
                                                <div className="class2_catalog_Name1" >{item.en_name.substr(0,1).toUpperCase()}</div>
                                                <div className="class2_catalog_Name2">{item.en_name.toLowerCase()}</div>
                                                <div className="class2_catalog_Name3">{item.name}</div>
                                            </div>
                                            <div className="class2_course_area">
                                            {
                                                item.courses.map((courseItem, courseIndex)=>{
                                                    var attr = '{"cid":"'+item.id+'","ciid":"'+courseItem.id+'"}'
                                                    let gifSrc = ""
                                                    for(let i=0;i<courseItem.coach_videos.length;i++){
                                                        if ( courseItem.coach_videos[i].coach_id === this.props.parent.state.coachid){
                                                            gifSrc = "./course"+courseItem.coach_videos[i].video_preview
                                                            break;
                                                        }
                                                    }

                                                    return <div className="class2_courseItem"
                                                            ref={(input) => { this.buttons[item.id+"_"+courseItem.id] = input }} href="#"
                                                            key={courseItem.id}
                                                            tabIndex={courseSeqIndex++}
                                                            row_index = {index}
                                                            row_max = {this.state.courseList.length}
                                                            col_index = {courseIndex}
                                                            col_max = {item.courses.length}
                                                            attr={attr}
                                                            selectcallback="mycallback"
                                                            selectcallbackparam={courseItem.title}
                                                        >
                                                        <img className="class2_courseImage" alt={courseItem.title} src={process.env.PUBLIC_URL + './course' + courseItem.image} ></img>

                                                        <div className="class2_courseName_wrapper" ></div>
                                                        <div className="class2_courseName_title" >{courseItem.title}</div>

                                                        <div className="class2_courseWrappImage_area">
                                                            <div className="class2_courseWrappImage_border" ></div>
                                                            <img className="class2_courseWrapperImage" src={gifSrc} alt={courseItem.title}/>
                                                        </div>
                                                    </div>
                                                })
                                            }
                                            {
                                                item.courses.length > 1 ?
                                                <div style={{marginLeft:80}}>&amps;</div>
                                                : null
                                            }

                                            </div>
                                        </div>
                            })
                        }
                        </div>

                        <video className="class2_tour" loop={true} controls={false} muted={true} autoPlay={true}>
                            <source src="/assets/horizontal.mp4"></source>
                        </video>

                    </div>
                </KeyboardEventHandler>
    }
}

export default Catalog;
