import React from 'react';

class DataService extends React.createElement {

    static data = null

    static async loadJSON(){
        var response = await fetch('./course/courses.json', {
            method: 'GET',
            headers:{
                Accept: 'application/json',
                'Content-Type': 'application/json'
            }
        });
        var body = await response.text();
        DataService.data = JSON.parse( body );
    }

    static getCoachList(){
        return DataService.data.coaches;
    }

    static async getCourseList2(coachid){

        let res = []
        for(let i =0;i<DataService.data.categories.length;i++){

            let category_item = DataService.data.categories[i];
            let result = {id:category_item.id, name:category_item.name, en_name:category_item.en_name}
            res.push(result);

            result.courses = DataService.data.courses.filter((item, index) =>{
                let result = item.coaches.indexOf(coachid) > -1 && item.course_categories.indexOf(category_item.id) > -1 ? item : null
                //console.log( result )
                if (result){
                    item.muscle = ''
                        for( var i=0;i<item.muscles.length;i++){
                            for (var j=0;j<DataService.data.muscles.length;j++){
                                if ( DataService.data.muscles[j].id === item.muscles[i]){
                                    item.muscle += (item.muscle.length > 0 ? '、':'')  + DataService.data.muscles[j].name
                                }
                            }
                        }
                }
                return result;
            })
        }
        return res;
    }

    static async getCourse(coachid, cid, ciid){
        let coach = DataService.data.coaches.filter((item, index) =>{
            return item.id === coachid ? item : null
        })[0];
        let category = DataService.data.categories.filter((item, index) =>{
            return item.id === cid ? item : null
        })[0];

        let course = DataService.data.courses.filter((item, index) =>{
            let result =  item.coaches.indexOf(coachid) > -1 && item.course_categories.indexOf(cid) > -1 && item.id === ciid ? item : null

            if (result ){
                let videos = result.coach_videos.filter((video,index)=>{
                    return video.coach_id === coachid ? video : null
                });

                result.video = videos[0].video
                result.video_skip = videos[0].video_start_point ? videos[0].video_start_point : 0

                result.second_video = videos[0].side_video
                result.second_video_skip = videos[0].video_start_point ? videos[0].video_start_point : 0

            }

            return result;
        })[0]

        course.coach = coach
        course.category = category

        return course;
    }
}

export { DataService };