import React from 'react';
import KeyboardEventHandler from 'react-keyboard-event-handler';
import tools from './util.js'

class ClassDone extends React.Component {
    constructor(props) {
        super(props);
        this.app = this.props.parent;
        if (this.props.parent.state.isDeveMode === true){
            this.app.state['user'] ={
                "course":{"title":"ddddd"} ,
                //"course" : this.app.state.user.course,
                "timer":"01:00",
                "count":12,
                "countAvgScore":99,
                "countTimes":[5000,4000,2000],
                "score":100,
                "elapsed_time":30,
                "start_Time":10,
                "end_time":40
            }
        }

        this.resetCounter = this.resetCounter.bind(this)

        let times = this.app.state.user.timer.split(":")
        //let avgtime = ( parseInt(times[0]) * 60 + parseInt(times[1]) ) / this.app.state.user.count
        let actionCountTime = 0
        //let actionScore = 0
        for (var i=0;i<this.app.state.user.countTimes.length;i++)
            actionCountTime += this.app.state.user.countTimes[i]
        //for (var j=0;j<this.app.state.user.score.length;j++){
        //    actionScore += this.app.state.user.score[j]
        //    console.log( "actionScore",actionScore )
        //}


        let avgtime = actionCountTime / 1000 / this.app.state.user.count

        let min = parseInt( avgtime / 60 )
        let sec = Math.round( (avgtime % 60) * 10) / 10
        this.state = { "countDown":30,
                        "handleKeys" : ['s','x','enter'],
                        "totalTime": times,
                        "avgTime": [min > 9 ? min  : '0'+min, sec > 9 ? sec : '0'+sec],
                        "countAvgTime": avgtime,
                        "countAvgScore" : this.app.state.user.score
                         }
        this.button2 = null;
        this.props.parent.currentPage = this;
        this.props.parent.setState({"availableKeys" : this.state.handleKeys})
    }

    resetCounter(){
        this.setState({"countDown":30})
    }

    async componentDidMount() {
        if(this.button2)
            this.button2.focus();
        this.setState({ "countDown": 30})

        if (this.props.parent.state.isDeveMode === false){
            var interval = window.setInterval(()=>{
                if ( this.state != null ){

                    var newCountDown = this.state.countDown -1
                    this.setState({ "countDown": newCountDown})
                    if ( newCountDown === 0){
                        this.app.changePage( this.app.state.pageIndex.Home )
                    }
                }
            }, 1000)

            this.setState({"interval":interval})

            tools.Util.sendLogScore(
            {
                "elapsed_time": this.app.state.user.elapsed_time,
                "start_time": this.app.state.user.start_time,
                "end_time": this.app.state.user.end_time,
                "course": this.app.state.user.course.title,
                "count": this.app.state.user.count,
                "average_time": this.state.countAvgTime,
                "correct_rate": this.state.countAvgScore,
                "user_id": this.app.state.user.userId,
                "age": this.app.state.user.age,
                "gender": this.app.state.user.gender,
                "coach": this.app.state.user.coach,
                "course_category": this.app.state.user.category,
            })
        }
    }

    componentWillUnmount(){
        if (this.props.parent.state.isDeveMode === false){
            if (this.state.interval)
                window.clearInterval( this.state.interval )
        }
    }

    async mycallback(title){
        if (this.props.parent.state.isDeveMode === false){
            this.app.ws.send( JSON.stringify({"function":"uploadCourse", "name":encodeURI(title)} ))
        }
    }

    render(){
        return (<KeyboardEventHandler
                handleKeys={this.state.handleKeys}
                onKeyEvent={(key, e, page) => this.props.util(key, e, this, page)} >
                <div style={{backgroundImage:'url(./assets/v_bg.jpg)', width:1080, height:1920}}>

                    <div className="classDone_Title">
                        您已完成{this.app.state.user.course.title}訓練
                    </div>

                    <div className="classDone_newCountArea">
                        <div className="classDone_Star">★★★</div>
                        <div className="classDone_innerArea">
                            <div className="classDone_inner2Area">
                                <div className="classDone_newFons"> 次數</div>
                                <div className="classDone_newNumberFons">{this.app.state.user.count}</div>
                                <div className="classDone_newFons2">/</div>
                                <div className="classDone_newFons">正確率</div>
                                <div className="classDone_newNumberFons">{this.state.countAvgScore}</div>
                            </div>
                        </div>
                    </div>

                    <div className="classDone_AvgTime_title">
                        每個動作平均時間
                    </div>
                    {
                        parseInt(this.state.avgTime[0]) > 0
                        ?
                        (
                            <div className="classDone_AvglTime_value">
                                {this.app.state.user.avgTime[0]}<div style={{fontSize:32,display:'inline'}}>mis</div>
                                {this.app.state.user.avgTime[1]}<div style={{fontSize:32,display:'inline'}}>sec</div>
                            </div>
                        )
                        :
                        (
                            <div className="classDone_AvglTime_value">
                                {this.state.avgTime[1]}<div style={{fontSize:32,display:'inline'}}>sec</div>
                            </div>
                        )
                    }

                    <div className="classDone_TotalTime_title">
                        總運動時間
                    </div>

                    {
                        parseInt(this.state.totalTime[0]) > 0
                        ?
                        (
                            <div className="classDone_TotalTime_value">
                                {this.state.totalTime[0]}<div style={{fontSize:32,display:'inline'}}>mis</div>
                                {this.state.totalTime[1]}<div style={{fontSize:32,display:'inline'}}>sec</div>
                            </div>
                        )
                        :
                        (
                            <div className="classDone_TotalTime_value">
                                {this.state.totalTime[1]}<div style={{fontSize:32,display:'inline'}}>sec</div>
                            </div>
                        )
                    }

                    <div className="classDone_qrcode_area" ></div>
                    <div className="classDone_qrcode_title">MOVE健身鏡滿意度調查</div>
                    <div className="classDone_qrcode_value"></div>

                    <button className="classDone_okButton"
                        tabIndex="2"
                        targetpage={this.app.state.pageIndex.ClassBegin}
                        selectcallback="mycallback"
                        selectcallbackparam={this.app.state.user.course.title}
                        ref={(input) => { this.button1 = input; }}
                    >再做一次</button>

                    <button className="classDone_endButton"
                        tabIndex="1"
                        targetpage={this.app.state.pageIndex.Class2}
                        ref={(input) => { this.button2 = input; }}
                    >結束</button>

                        <video className="class2_tour" loop={true} controls={false} muted={true} autoPlay={true}>
                            <source src="/assets/horizontal.mp4"></source>
                        </video>
                </div>
            </KeyboardEventHandler>)
    }
}

export default ClassDone;
